.cell{border:1px solid #a9a9a9;user-select:none;width:25px;max-width:25px;height:25px;max-height:25px;overflow:hidden;cursor:pointer;text-align:center;font-weight:700;background-color:#e1e1e1}.cell:not(._open){box-shadow:inset 3px 3px 1px #fff,inset -3px -3px 1px #c5c5c5}.cell._open{cursor:default}.cell._bomb._open{background-image:url("../../assets/bomb.png");background-color:rgba(234,42,42,.709);background-repeat:no-repeat;background-size:contain}.cell._open._bombs-nearby[data-bombs-nearby="1"]{color:blue}.cell._open._bombs-nearby[data-bombs-nearby="2"]{color:green}.cell._open._bombs-nearby[data-bombs-nearby="3"]{color:red}.cell._open._bombs-nearby[data-bombs-nearby="4"]{color:darkblue}.cell._open._bombs-nearby[data-bombs-nearby="5"]{color:darkmagenta}.cell._open._bombs-nearby[data-bombs-nearby="6"]{color:slateblue}.cell._open._bombs-nearby[data-bombs-nearby="7"]{color:blueviolet}.cell._open._bombs-nearby[data-bombs-nearby="8"]{color:darkorange}.cell._open._bombs-nearby[data-bombs-nearby="9"]{color:darkred}.cell._flag{background-image:url("../../assets/flag.png");background-repeat:no-repeat;background-size:75%;background-position:center}