.settings{position:fixed;width:300px;inset:0;background-color:#fff;z-index:1;transform:translateX(-100%);transition-property:transform,box-shadow;transition-duration:500ms;transition-timing-function:ease-in-out}.settings__section{padding:8px 12px}.settings__block~.settings__block{margin-top:8px}.settings__submit{padding:0 12px 8px}.settings_opened{border-right:2px solid #d3d3d3;box-shadow:5px 0 5px #d3d3d3;transform:translateX(0)}.settings_button-close{position:absolute;right:13px;top:13px}